*{
    font-family: Helvetica, Arial, sans-serif;
}

body{
    padding: 60px;
}

input{
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid{
     display: flex;
     flex-direction: row;
     flex-wrap: wrap;
}

 .card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
   
    margin-bottom: 10px;
    margin-right: 10px;
    /* Desbordamiendo, con el hidden se corta la imagen, y con el auto se coloca
    un scroll para mostrar la imagen */
    overflow: hidden;

 }

 .card p{
     padding: 5px;
     text-align: center;
 }

 .card img{
     max-height: 170px;
 }

 